'use client';
import React, { useState } from 'react';
import { Calendar, Clock, Users, Home, BookOpen, DoorOpen, Menu, X, School, DoorClosed } from 'lucide-react';

// Import all page components
import DashboardPage from '@/Components/DashboardPage/dashboard';
import TimetablePage from '@/Components/TimetablePage/timetable';
import RoomsPage from '@/Components/RoomsPage/rooms';
import LecturersPage from '@/Components/LecturersPage/lecturers';
import ClassesPage from '@/Components/ClassesPage/classes';
import CoursePage from '@/Components/CoursePage/courses';
import FacultyPage from '@/Components/FacultyPage/faculty';
import ProgramPage from '@/Components/ProgramPage/program';

const TimetableManagementDashboard = () => {
  const [activePage, setActivePage] = useState('dashboard');
  const [menuOpen, setMenuOpen] = useState(false);

  const toggleMenu = () => setMenuOpen(!menuOpen);

  // Page content components
  const renderPageContent = () => {
    switch (activePage) {
      case 'dashboard': return <DashboardPage />;
      case 'timetable': return <TimetablePage />;
      case 'faculty': return <FacultyPage />;
      case 'program': return <ProgramPage />;
      case 'lecturers': return <LecturersPage />;
      case 'courses': return <CoursePage />;
      case 'classes': return <ClassesPage />;
      case 'rooms': return <RoomsPage />;
      default: return <DashboardPage />;
    }
  };

  return (
    <div className="flex flex-col min-h-screen bg-gray-100">
      {/* Sticky Header */}
      <header className="fixed top-0 left-0 w-full bg-gradient-to-r from-blue-500 to-indigo-700 text-white shadow-md z-50">
        <div className="container mx-auto flex items-center justify-between px-6 py-4">
          {/* Logo */}
          <h1 className="text-2xl font-bold flex items-center">
            <Clock className="h-6 w-6 mr-2" /> Timetable System
          </h1>

          {/* Hamburger Menu (for small screens) */}
          <button onClick={toggleMenu} className="lg:hidden block text-white focus:outline-none">
            {menuOpen ? <X className="h-8 w-8" /> : <Menu className="h-8 w-8" />}
          </button>

          {/* Desktop Navigation */}
          <nav className="hidden lg:flex">
            <ul className="flex space-x-6 text-lg font-medium">
              <NavItem title="Dashboard" isActive={activePage === 'dashboard'} onClick={() => setActivePage('dashboard')} />
              <NavItem title="Timetable" isActive={activePage === 'timetable'} onClick={() => setActivePage('timetable')} />
              <NavItem title="Faculty" isActive={activePage === 'faculty'} onClick={() => setActivePage('faculty')} />
              <NavItem title="Program" isActive={activePage === 'program'} onClick={() => setActivePage('program')} />
              <NavItem title="Lecturers" isActive={activePage === 'lecturers'} onClick={() => setActivePage('lecturers')} />
              <NavItem title="Course" isActive={activePage === 'courses'} onClick={() => setActivePage('courses')} />
              <NavItem title="Classes" isActive={activePage === 'classes'} onClick={() => setActivePage('classes')} />
              <NavItem title="Rooms" isActive={activePage === 'rooms'} onClick={() => setActivePage('rooms')} />
            </ul>
          </nav>
        </div>

        {/* Mobile Dropdown Navigation */}
        {menuOpen && (
          <div className="lg:hidden bg-indigo-800 text-white text-center py-4">
            <ul className="space-y-4">
              <NavItem title="Dashboard" isActive={activePage === 'dashboard'} onClick={() => { setActivePage('dashboard'); setMenuOpen(false); }} />
              <NavItem title="Timetable" isActive={activePage === 'timetable'} onClick={() => { setActivePage('timetable'); setMenuOpen(false); }} />
              <NavItem title="Faculty" isActive={activePage === 'faculty'} onClick={() => { setActivePage('faculty'); setMenuOpen(false); }} />
              <NavItem title="Program" isActive={activePage === 'program'} onClick={() => { setActivePage('program'); setMenuOpen(false); }} />
              <NavItem title="Lecturers" isActive={activePage === 'lecturers'} onClick={() => { setActivePage('lecturers'); setMenuOpen(false); }} />
              <NavItem title="Course" isActive={activePage === 'courses'} onClick={() => { setActivePage('courses'); setMenuOpen(false); }} />
              <NavItem title="Classes" isActive={activePage === 'classes'} onClick={() => { setActivePage('classes'); setMenuOpen(false); }} />
              <NavItem title="Rooms" isActive={activePage === 'rooms'} onClick={() => { setActivePage('rooms'); setMenuOpen(false); }} />
            </ul>
          </div>
        )}
      </header>

      {/* Page Content */}
      <main className="mt-20 p-6 flex-1 overflow-y-auto">
        {renderPageContent()}
      </main>
    </div>
  );
};

// Navigation Item Component
const NavItem = ({ title, isActive, onClick }) => {
  return (
    <li>
      <button
        onClick={onClick}
        className={`px-4 py-2 rounded-md transition-colors ${
          isActive ? 'bg-white text-blue-700' : 'hover:bg-blue-600'
        }`}
      >
        {title}
      </button>
    </li>
  );
};

export default TimetableManagementDashboard;
